import AbstractColorScheme from "./AbstractColorScheme";
export default class GrayscaleColorScheme extends AbstractColorScheme {
    constructor() {
        super(...arguments);
        this.label = 'Grayscale';
        this.interpolate = true;
    }
    getColorableDefault() {
        return '#fff';
    }
    getColorSteps() {
        return {
            0: this.getColorable(),
            1: this.getColorableDarkest()
        };
    }
}
